






































































































































































































































































































@use "~main/theme"

.menu-bar
  background-color: rgb(var(--color-surface-secondary))
  padding: 0.75rem 1.25rem

#tl-fullscreen-modal
  .modal-full
    max-width: 100%

  .modal-body
    padding: 0

  .video-js
    height: calc(100vh - 200px)

::v-deep .btn-outline-secondary
  color: rgb(var(--color-text-primary))
  border-color: rgb(var(--color-text-primary))

  &:hover
    background: none
    opacity: .8
